<template>
  <div id="headers">
    <div class="headertoer"> </div>
    <div class="header w-1210">
      <div class="header-logo ">
        <img src="../../../../static/img/logo.png" />
      </div>
      
      <div class="header-navigation">
        <ul class="header-navigation-ul f-14">
          <li>
            <a href="/">
              <div :class="[date==''?csse:'']" @click="lidataclck('')"></div>
            </a>
          </li>
          <li>
            <router-link to="/Kgold">
              <div :class="[date=='K '?csse:'']" @click="lidataclck('K ')">K  </div>
            </router-link>
          </li>
          <li>
            <router-link to="/silverware">
              <div :class="[date==''?csse:'']" @click="lidataclck('')"> </div>
            </router-link>
          </li>
          <li>
            <router-link to="/jewelryDesign">
              <div :class="[date==''?csse:'']" @click="lidataclck('')"> </div>
            </router-link>
          </li>
          <li>
            <router-link to="/brands">
              <div :class="[date==''?csse:'']" @click="lidataclck('')"> </div>
            </router-link>
          </li>
          <li>
            <router-link to="/interactive">
              <div :class="[date==''?csse:'']" @click="lidataclck('')"> </div>
            </router-link>
          </li>
          <li>
            <router-link to="#">
              <div :class="[date==''?csse:'']" @click="lidataclck('')"> </div>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <div style="border-top:2px solid #000;height: 0px;"></div>
  </div>
</template>
<script>
export default {
    name: "headers",
    data() {
        return {
            csse: "clke",
            date: "",
        };
    },
    created: function() {
        this.fetchData();
    },
    methods: {
        fetchData: function() {
            var _self = this;
            _self.$nextTick(function() {
                //   home_banner();
            });
        },
        lidataclck: function(index) {
          //
            this.date = index;
        }
    }
};
</script>
<style scoped>

.headertoer {
    width: 100%;
    height: 14px;
    background: #9ed6ca;
}
.header .header-logo {
    margin-top: 20px;
}
.header .header-navigation {
    width: 757px;
    text-align: center;
    margin: 0px auto;
    margin-top: 20px;
    clear: both;
}

.header .header-navigation-ul {
    width: 757px;
    clear: both;
    height: 31px;
    /* display: flex;
    justify-content: space-around; */
}
.header .header-navigation li {
  float: left;
  margin-left: 45px;
    width: 60px;
    height: 31px;
    color: #333;
}
.header .header-navigation li div {
    height: 27px;
}

.header .header-navigation li div:hover {
    color: #91d6c9;
    border-bottom: 4px solid #91d6c9;
    font-size: 16px;
}

.header .clke {
    color: #91d6c9;
    border-bottom: 4px solid #91d6c9;
    font-size: 16px;
}
</style>



